import React from "react"
import { Link } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"

export default () => (
    <Layout>
    <SEO title="Home" />
    <div>
        <h1>DollZでの変更点(ver5.0.16a)</h1>
        <h2>追加要素</h2>
        
        <h3>インターフェース</h3>
        <p>スコアボードのサイドバーを使用。</p>
        <ul>
            <li>座標を常に表示するように。F3を使用する必要はありません！</li>
            <li>喉の渇きをインターフェースに表示するように変更。%で表されます</li>
        </ul>
        <h3>状態異常</h3>
        <ul>
            <li>体温を追加。風邪に注意。</li>
            <li>感染症を追加。ゾンビからもらう確率は極めて低いです。</li>
            <li>病気を追加。</li>
        </ul>
        <h3>視覚情報</h3>
        <p>状態異常や特殊なアイテムによって、画面がやや変化します。</p>
        <p>血液不足</p>
        <p>麻薬</p>
        <p>監視カメラ</p>

        <h3>アイテム</h3>
        <p>更新によって、大量にアイテムが追加されました。</p>
        <h4>布切れ・包帯</h4>
        <p>止血することができます。また、他プレイヤーに使用することもできます</p>
        <h4>鎮痛剤</h4>
        <p>骨折を治すことができます。また数秒間の耐性を付与します。</p>
        <h4>バックパック</h4>
        <p>インベントリを増やすことができます。</p>
        <h4>トランシーバー</h4>
        <p>指定したチャンネル内で会話することができます。</p>
        <h4>缶詰</h4>
        <p>食料ケージを回復します。</p>
        <h4>貴重なたんぱく源</h4>
        <p>食べれますがロクなことになりません。</p>
        <h4>ビタミン剤や抗生物質</h4>
        <p>病気や感染症を治します。</p>

        <h3>ファットゾンビ</h3>
        <p>
            体力は1(♥×0.5)ですが死亡時に大きな爆発を起こします。<br/>
            ダンジョンや接敵率が低い場所にスポーンします。
        </p>

        <h3>バンディット</h3>
        <p>クロスボウ構えてるアイツ。</p>

        <h2>変更要素</h2>

        <h3>ゾンビ</h3>
        <ul>
            <li>町/建築物以外でのスポーン率を大幅に下方修正。また接敵率が高い場所でのスポーン率を下方修正。</li>
            <li>通常ゾンビの体力を10(♥×5)に変更。増援率を下方修正</li>
            <li>子供ゾンビの体力を1(♥×0.5)に変更。</li>
        </ul>
        <h3>状態異常</h3>
        <ul>
            <li>出血の確率が0.02%に低く設定されていた不具合の修正</li>
            <li>出血の仕組みを変更。銃によるダメージの場合は20%, 近接攻撃(ゾンビを含む)によるダメージの場合は5%に変更</li>
            <li>骨折の確率が0%に設定されていた不具合の修正</li>
            <li>骨折の仕組みを変更。(落下距離×2)%で骨折するように変更</li>
        </ul>
        <h3>アイテム</h3>
        <ul>
            <li>Hook Rodの仕様を変更。カスタムや不可壊などの効果を削除。</li>
        </ul>
        <h3>パフォーマンス</h3>
        <p>コア処理を全て書き換え、パフォーマンスを上げました。</p>

        <h2>バージョンについて</h2>
        <p>
            verA.B.Cと表記されます。初代(3年前)をver1.0.0として、更新を続けています。<br/>
            Aの部分が変わるたびに、コードの互換性は90%以上失われています。<br/>
            ver5のコードはオープンソースとなる予定。やったね。
        </p>
    </div>
  </Layout>
)
